import dayjs from "dayjs";
import commentTextarea from "~/components/commentTextarea";
import { mapState } from "vuex";
export default {
  props: {
    blogsId: String,
    model: String,
    commentsList: Array
  },
  components: {
    commentTextarea
  },
  data() {
    return {
      placeholder: '',
      userInfo: '',
      showReply: false,
      dialog: false,
      loading: false,
      parentId: '',
      byAiteName: "",
      byAiteUserId: '',
      commentIndex: -1,
      total: 31,
      pageNum: 1,
      pageSize: 10,
      clientWidth: 10000,
    };
  },
  created() {

  },
  beforeMount() {
    this.clientWidth = document.body.clientWidth
        this.userInfo = (localStorage.userInfo &&
        JSON.parse(localStorage.userInfo)) || { _id: "" }
  },
  filters: {
    formatDate(val) {
      return dayjs(val).format("YYYY-MM-DD HH:MM:ss");
    }
  },
  computed: {
    ...mapState(["map_user_info"])
  },
  methods: {
    replyHandle(item, index, c) {
      this.placeholder = c ? c.nickName : item.nickName;
      this.commentIndex = index;    
      // this.showReply = !this.showReply
      this.parentId = item._id
      this.byAiteName = c ? c.nickName : item.nickName;
      this.byAiteUserId = c ? c.userId : item.userId;
      
      // 如果设备屏幕宽度<=375, 回复框使用弹出el-drawer
      if (this.clientWidth <= 450) {
        this.dialog = true
      } else {
        this.showReply = true;
      }
    },
    hideReply() {
      this.commentIndex = -1;
      this.dialog = false
      this.$emit("getCommentList");
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    }, 
    handleClose(){
      this.dialog = false
    }
  }
};
